*{box-sizing:border-box}body{background-color:#000}.wrap{color:#dadada;font-family:Raleway,-apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Oxygen,Ubuntu,Cantarell,Open Sans,Helvetica Neue,sans-serif;text-align:center;max-width:70rem;margin:0 auto;overflow:hidden;position:relative;min-height:100vh;display:flex;flex-direction:column}.c-header{font-family:Playfair Display,sans-serif;padding:1.5em 1em 0}.c-header__title{font-size:3.5rem;margin:0 0 .4em;letter-spacing:2px}.c-header__description{font-size:1.1rem;margin:0 0 2em}@media screen and (min-width:20rem){.c-header__title{font-size:calc(3.5rem + (250vw - 50rem)/50)}.c-header__description{font-size:calc(1.1rem + (90vw - 18rem)/50)}}@media screen and (min-width:70rem){.c-header__title{font-size:6rem}.c-header__description{font-size:2rem}}.c-miniatures{list-style-type:none;display:flex;justify-content:space-around;align-items:center;flex-direction:column;padding:0;flex-grow:1}.c-miniatures__miniature{width:100%;max-width:35rem;background-color:#dadada;background-image:url(./images/flower.jpg);-webkit-filter:grayscale(100%);filter:grayscale(100%);background-repeat:no-repeat;background-position:50%;background-size:cover;border-radius:8px}.c-miniatures__link{min-height:2rem;display:block;text-decoration:none;color:#dadada;background-color:#000;opacity:0;transition:.2s;display:flex;align-items:center;justify-content:center}.c-miniatures__link:active,.c-miniatures__link:focus{outline:10px dotted #fff;opacity:1}.c-miniatures__link:hover{opacity:1}@media screen and (min-height:35.5rem){.c-miniatures__link{min-height:calc(2rem + (200vh - 71rem)/49.875)}}@media screen and (min-height:85.375rem){.c-miniatures__link{min-height:4rem}}@media screen and (min-width:70rem),screen and (orientation:landscape){.c-miniatures{flex-direction:row;flex-wrap:nowrap;align-items:stretch}.c-miniatures__miniature{flex-basis:15%}.c-miniatures__link{height:100%}}.c-relic{border-bottom:3px solid #fff}.c-relic:last-child{border-bottom:none}.c-relic--js{position:absolute;top:50%;left:100%;width:100%;-webkit-transform:translateY(-50%);transform:translateY(-50%)}.c-relic__name{font-size:1.4rem}.c-relic__description{font-size:1.2rem;text-align:justify}@media screen and (min-width:20rem){.c-relic__name{font-size:calc(1.4rem + (20vw - 4rem)/50)}.c-relic__description{font-size:calc(1.2rem + (10vw - 2rem)/50)}}@media screen and (min-width:70rem){.c-relic__name{font-size:1.6rem}.c-relic__description{font-size:1.3rem}}.c-footer__text{font-size:1.2rem;margin:1.2em 0}@media screen and (min-width:20rem){.c-footer__text{font-size:calc(1.2rem + (30vw - 6rem)/50)}}@media screen and (min-width:70rem){.c-footer__text{font-size:1.5rem}}.l-main-content{width:90%;margin:0 auto}.l-main-content,.l-main-menu{display:flex;flex-direction:column;flex-grow:1}.l-main-menu{justify-content:center}.l-relics{padding:0 1rem}